import parse from 'html-react-parser';
import Modal from '../../shared/Modal'

import isElementInViewPort from '../../helpers/percentageOfElementInViewPort';

import { useState, useRef } from 'react'
function ItemContainer(props){

    const ItemContainerRef = useRef(null)
    
    const [showModal, setShowModal] = useState(false)
    const itemHTML = props.itemHTML
    const margin = props.margin

    let marginBottom


    function handleClick() { setShowModal(true) }
    function closeModal() { setShowModal(false)}

    let renderThumbNail = true
    isElementInViewPort(ItemContainerRef) ? renderThumbNail = true : renderThumbNail = false

    return (
        <div 
            ref={ItemContainerRef}    
            className='flex flex-column bg-white mr1 ba b--dark-purple bw1 br2 shadow-4' 
            style={{height: '30vh', marginTop: '1vh', marginBottom: '1vh'}}>
                {showModal &&
                    <Modal clickOk={closeModal}>
                            <div dangerouslySetInnerHTML={{__html:itemHTML}}></div>
                    </Modal>}
            <div
                onClick={handleClick} 
                className='bg-white mt2 mb2 f6 pointer' 
                style={{
                    overflow: 'hidden',
                    marginLeft: `${margin}px`, 
                    marginRight: `${margin}px`,
                    marginBottom: marginBottom}}>
                    {renderThumbNail 
                        && <div>{parse(itemHTML)}</div>}
            </div>
        </div>
    )
}

export default ItemContainer