function PurpleButton(props){
    const buttonText = props.buttonText
    const svgSrc = props.svgSrc
    return (
        <button className='bg-dark-purple ba white pa1 pl3 pr3 b--dark-purple pointer grow flex flex-row justify-center items-center'>
            {svgSrc &&
            <div className='mr3'>
              <img src={svgSrc} width='20px' height='20px'/>
            </div>}
            <div>
                {buttonText}
            </div>
        </button>
    )
}

export default PurpleButton;